.section {
   &__question {
      background: #ACB992;
      position: relative;
      &::after{
         content: url(..//assets/img/question_bg1.webp);
         position: absolute;
         top: 83px;
         left: 0;
         width: 729.73px;
         height: auto;
         z-index: -1 !important;
         @media (max-width: 1280px){
            width: 700px;
         }
         @media (max-width: 992px){
            width: 500px;
         }
         @media (max-width: 768px){
            display: none;
         }
      }
      &::before{
         content: url(..//assets/img/question_bg2.webp);
         position: absolute;
         top: 0;
         right: -653px;
         width: 1079.73px;
         height: auto;
         z-index: -1 !important;
         @media (max-width: 1280px){
            width: 700px;
            right: -300px;
         }
         @media (max-width: 992px){
            width: 500px;
         }
         @media (max-width: 768px){
            display: none;
         }
      }
   }
}
.question {
   p {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      width: 100%;
      min-width: 100%;
      gap: 51px;
      z-index: 99;
      @media (max-width: 768px){
         gap: 20px;
         max-width: 500px;
         width: 100%;
         margin: 0 auto;
      }
      span{
         width: 100%;
         flex: 0 1 30%;
         &:last-child{
            display: none;
         }
         @media (max-width: 768px){
            flex: 0 1 100%;
         }
      }
      br{
         display: none !important;
      }
   }
   &__form-input {
      background: #ACB992;
      border: 1px solid #464E2E;
      padding: 13px 20px;
      font-family: 'Raleway', sans-serif;
      font-style: normal;
      font-weight: 200;
      font-size: 16px;
      line-height: 19px;
      letter-spacing: 0.1em;
      color: #FFFFFF;
      z-index: 99;
      width: 100%;
      &::placeholder{
         color: #FFFFFF;
      }
      &::-moz-placeholder{
         color: #FFFFFF;
      }
   }
   &__form-btn {
      padding: 13px 73px;
      border: none;
   }
}