.footer {
   padding: 57px 0;
   position: relative;
   overflow: hidden;
   &::after{
      content: url(..//assets/img/circle.svg);
      position: absolute;
      top: -47px;
      right: 0;
      z-index: 0;
      
      @media (max-width: 768px){
         display: none;
      }
   }
   &__logo {
      display: flex;
      justify-content: center;
      margin-bottom: 57px;
   }
   &__menu {
      display: flex;
      justify-content: space-between;
      margin-bottom: 57px;
      @media (max-width: 768px){
         flex-direction: column;
         gap: 40px;
         justify-content: center;
         text-align: center;
      }
   }
   &__list{
      &:nth-child(2){
         .footer__menu-item{
             margin-bottom: 31px;
             max-width: 275px;
             @media (max-width: 768px){
               margin: 0 auto 20px;
            }
         }
      }
      &:last-child{
         .footer__menu-item{
            text-decoration: underline;
         }
      }
   }
   &__menu-item {
      margin-bottom: 12px;
   }
   &__text a{
      font-style: normal;
      font-weight: 400;
      color: #000000;
   }
   &__copyright {
      text-align: center;
   }
   &__copy {
      font-family: 'Raleway', sans-serif;
      font-style: normal;
      font-weight: 400;
      font-size: 12px;
      line-height: 14px;
      color: #000000;
      margin-top: 22px;
      span{
         color: #464E2E;
      }
   }
}