header {
   background: #acb992;
   position: relative;
   overflow: hidden;
   .decor {
      position: absolute;
      height: 100%;
      left: 50%;
      transform: translate(-50%, 0);
      z-index: 1;
   }
   .line {
      position: absolute;
      width: 1px;
      height: 100%;
      top: 0;
      background: #ffffff;
   }
   .line1 {
      left: 34px;
      @media (max-width: 480px) {
         left: 10px;
      }
   }
   .line3 {
      left: 213px;
      @media (max-width: 768px) {
         left: 100px;
      }
      @media (max-width: 480px) {
         left: 50px;
      }
   }
   .line4 {
      left: 50%;
      transform: translate(-50%, 0);
   }
   .line2 {
      position: absolute;
      width: 1px;
      height: 100%;
      right: 34px;
      top: 0;
      background: #464e2e;
   }
   &::after {
      content: url(..//assets/img/start_bg.webp);
      position: absolute;
      top: -137px;
      right: -249px;
      width: 1223px;
      height: auto;
      background-color: #ffffff;
      z-index: 0;
      @media (max-width: 1280px) {
         width: 800px;
      }
      @media (max-width: 992px) {
         width: 500px;
         right: 0px;
         top: 0px;
      }
      @media (max-width: 768px) {
         display: none;
      }
   }
   nav {
      position: relative;
      z-index: 2;
   }
   .fixed__nav {
      position: fixed;
      left: 0;
      right: 0;
      top: 0;
      background: #acb992;
      z-index: 999;
      transition: all 0.3s ease;
      .navbar {
         padding: 15px 0;
         transition: all 0.3s ease;
      }
   }
   .navbar {
      display: flex;
      width: 100%;
      justify-content: space-between;
      align-items: center;
      padding: 37px 0;
      transition: all 0.3s ease;
   }
   .logo {
      width: 196px;
      img {
         width: 100%;
         max-width: 100%;
         height: auto;
      }
      @media (max-width: 992px) {
         width: 150px;
      }
   }
   .menu {
      width: 100%;
      display: flex;
      justify-content: space-between;
      align-items: center;
      @media (max-width: 768px) {
         background: #acb992;
         display: none;
         &.active {
            display: flex;
            position: fixed;
            right: 0;
            bottom: 0;
            left: 0;
            top: 0;
            height: 100vh;
            width: 100vw;
            flex-direction: column;
            pointer-events: all;
            justify-content: center;
            align-items: center;
            gap: 40px;
            z-index: 22;
            animation: fade 0.3s ease;
         }
      }
   }
   .list {
      justify-content: center;
      display: flex;
      align-items: center;
      gap: 37px;
      margin: 0 auto;
      @media (max-width: 768px) {
         flex-direction: column;
         gap: 25px;
      }
      &__item {
         transition: all 0.3s ease;
         color: #000;
         @media (max-width: 768px) {
            font-size: 25px !important;
         }
         &:hover {
            opacity: 0.6;
            transition: all 0.3s ease;
            color: #000;
         }
      }
   }
   .burger {
      @media (max-width: 768px) {
         display: block;
         position: relative;
         height: 20px;
         width: 30px;
         z-index: 102;
         cursor: pointer;
         &.active-burger {
            &::before {
               transform: rotate(45deg);
               transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
               top: 8px;
            }
            &::after {
               transform: rotate(-45deg);
               transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
               bottom: 10px;
            }
            & span {
               transform: scale(0);
            }
         }
         &::before,
         &::after {
            content: "";
            position: absolute;
            height: 2px;
            width: 100%;
            background-color: #ffffff;
            transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
         }
         &::before {
            top: 0;
         }
         &::after {
            bottom: 0;
         }
         span {
            position: absolute;
            top: 9px;
            right: 0;
            width: 100%;
            background-color: #ffffff;
            height: 2px;
            transform: scale(1);
            transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s all;
         }
      }
   }
   .btn {
      padding: 12px 47px;
   }
   .start {
      z-index: 1;
      padding: 245px 0 244px;
      @media (max-width: 768px) {
         padding: 50px 0;
         &__content {
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
         }
      }
      .container {
         position: relative;
         z-index: 1;
      }
      &__title {
         font-family: "Swear Display Regular";
         font-style: normal;
         font-weight: 300;
         font-size: 67.375px;
         line-height: 89px;
         letter-spacing: 0.28em;
         color: #000000;
         margin-bottom: 43px;
         @media (max-width: 768px) {
            margin-bottom: 20px;
            text-align: center;
            font-size: 48px;
            line-height: 1.4;
         }
         @media (max-width: 480px) {
            font-size: 36px;
         }
      }
      &__btn {
         padding: 16px 70px;
         @media (max-width: 768px) {
            position: relative;
            right: 0;
            top: 0;
            transform: translate(0, 0);
         }
      }
      &__item {
         background: #ffffff;
         padding: 45px 18px;
         max-width: 352px;
         width: 100%;
         height: auto;
         position: absolute;
         right: 247px;
         top: 50%;
         transform: translate(0, -50%);
         z-index: -1;
         .circle {
            position: absolute;
            left: -63px;
            bottom: -53px;
            z-index: -1;
            @media (max-width: 480px) {
               left: 50%;
               bottom: -100px;
               transform: translate(-50%, 0);
            }
         }
         .start__img {
            width: 100%;
         }
         @media (max-width: 1212px) {
            right: 200px;
         }
         @media (max-width: 992px) {
            right: 0;
         }
         @media (max-width: 768px) {
            position: relative;
            top: 0;
            transform: translate(0, 0);
            margin: 40px auto 0;
         }
      }
   }
}
