.gallery {
   display: grid;
   grid-template-columns: repeat(3, 1fr);
   grid-template-rows: repeat(3, 1fr);
   grid-column-gap: 33px;
   grid-row-gap: 35px;
   @media (max-width: 768px){
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(4, 1fr);
   }
   @media (max-width: 480px){
      grid-template-columns: 1fr;
   }
   &__item {
      width: 100%;
      position: relative;
      height: 100%;
      padding: 107px 0;
      &::after{
         content: '';
         position: absolute;
         left: 0;
         top: 0;
         bottom: 0;
         right: 0;
         background: rgba(255, 255, 255, 0.01);
         backdrop-filter: blur(10px);
         opacity: 0;
         z-index: 0;
         transition: all .3s ease;
      }
      @media (max-width: 992px){
         padding: 70px 0;
      }
      @media (max-width: 480px){
         grid-area: auto !important;
         padding: 107px 0;
      }
      &:nth-child(1){
         grid-area: 1 / 1 / 2 / 2; 
      }
      &:nth-child(2){
         grid-area: 1 / 2 / 2 / 3;
         @media (max-width: 768px){
            grid-area: 1 / 2 / 3 / 3;
         }
      }
      &:nth-child(3){
         grid-area: 1 / 3 / 3 / 4; 
         @media (max-width: 768px){
            grid-area: 2 / 1 / 4 / 2; 
         }
      }
      &:nth-child(4){
         grid-area: 2 / 1 / 4 / 2;
         @media (max-width: 768px){
            grid-area: 3 / 2 / 4 / 3;
         }
      }
      &:nth-child(5){
         grid-area: 2 / 2 / 4 / 3;
         @media (max-width: 768px){
            grid-area: 4 / 1 / 5 / 2;
         }
      }
      &:nth-child(6){
         grid-area: 3 / 3 / 4 / 4;
         @media (max-width: 768px){
            grid-area: 4 / 2 / 5 / 3; 
         }
      }
      &:hover{
         &::after{
            transition: all .3s ease;
            opacity: 1 !important;
         }
         .gallery__item-btn{
            z-index: 1;
            opacity: 1;
            visibility: visible;
         }
      }
   }
   &__item-front{
      position: absolute;
      left: 0;
      top: 0;
      bottom: 0;
      right: 0;
      width: 100%;
      height: 100%;
      z-index: 0;
      img{
         width: 100%;
         height: 100%;
         object-fit: cover;
      }
   }
   &__item-back{
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
   }
   &__item-btn {
      opacity: 0;
      visibility: hidden;
      padding: 12px 54px;
      color: #ffffff;
      border: 1px solid #fff;
      &:hover{
         background-color: #fff;
         color: #464E2E;
      }
   }
}