*{
   margin: 0;
   padding: 0;
   box-sizing: border-box;
}
:focus-visible{
	outline: none;
}
body{
   .overlay{
      width: 100%;
      overflow: hidden;
		display: flex;
		flex-direction: column;
		height: 100%;
		min-height: 100vh;
   }
}
.main{
	flex: 1 1 auto;
} 
.container{
   max-width: $mw;
   margin: 0 auto;
   width: 100%;
   @media (max-width: $md1){
      max-width: 970px;
   }
   @media (max-width: $md2){
      max-width: 750px;
   }
   @media (max-width: $md3){
      max-width: none;
      padding: 0 10px;
   }
}
a{
   text-decoration: none;
}
h1, h2, h3, h4, p, span {
   margin: 0;
   padding: 0;
}
ul, li {
	list-style: none;
   padding: 0;
   margin: 0;
}
.btn{
   font-family: 'Raleway', sans-serif;
   font-style: normal;
   font-weight: 300;
   font-size: 14px;
   line-height: 16px;
   letter-spacing: 0.1em;
   color: #FFFFFF;
   background: #464E2E;
   transition: all .3s ease;
   &:hover, &:active, &:visited, &:focus-visible, &:focus{
      background: #333822;
      transition: all .3s ease;
   }
}
.btn__outline{
   font-family: 'Raleway', sans-serif;
   font-style: normal;
   font-weight: 300;
   font-size: 16px;
   line-height: 19px;
   letter-spacing: 0.1em;
   border: 1px solid;
   transition: all .3s ease;
   &:hover, &:active, &:visited, &:focus-visible, &:focus{
      transition: all .3s ease;
   }
}
.title{
   font-family: 'Raleway', sans-serif;
   font-style: normal;
   font-weight: 400;
   font-size: 22px;
   line-height: 26px;
   letter-spacing: 0.1em;
   color: #464E2E;
   margin-bottom: 57px;
   text-align: center;
   z-index: 1;
}
.subtext{
   font-family: 'Raleway', sans-serif;
   font-style: normal;
   font-weight: 300;
   font-size: 14px;
   line-height: 16px;
   color: #000000;
}
.section{
   padding: 57px 0;
}
// page animation
.js-scroll {
	opacity: 0;
	transition: opacity 0ms;
	&.scrolled {
		opacity: 1;
		&.fade-in {
			animation: fade-in 1s ease-in-out 0s;
		}
		&.slide-in-left {
			animation: slide-in-left 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: slide-in-top 0.6s ease-in-out 0s;
			}
		}
		&.slide-in-top {
			animation: slide-in-top 1s ease-in-out 0s;
		}
		&.slide-in-bottom {
			animation: slide-in-bottom 1s ease-in-out 0s;
		}
		&.slide-in-right {
			animation: slide-in-right 1s ease-in-out 0s;
			@media (max-width: 480px) {
				animation: fade-in-top 0.6s ease-in-out 0s;
			}
		}
	}
}

@keyframes slide-in-left {
	0% {
		transform: translateX(-100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes slide-in-top {
	0% {
		transform: translateY(-100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-bottom {
	0% {
		transform: translateY(100px);
		opacity: 0;
	}
	100% {
		transform: translateY(0);
		opacity: 1;
	}
}
@keyframes slide-in-right {
	0% {
		transform: translateX(100px);
		opacity: 0;
	}
	100% {
		transform: translateX(0);
		opacity: 1;
	}
}
@keyframes fade-in {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}
@media (max-width: 480px){
	@keyframes slide-in-left {
		0% {
			transform: translateX(-50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-top {
		0% {
			transform: translateY(-50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-bottom {
		0% {
			transform: translateY(50px);
			opacity: 0;
		}
		100% {
			transform: translateY(0);
			opacity: 1;
		}
	}
	@keyframes slide-in-right {
		0% {
			transform: translateX(50px);
			opacity: 0;
		}
		100% {
			transform: translateX(0);
			opacity: 1;
		}
	}
}