.section {
   &__about {
      background: #ACB992;
      padding: 57px 0 126px;
      position: relative;
      &::after{
         content: url(..//assets/img/about_bg.webp);
         position: absolute;
         top: 50%;
         right: 50%;
         transform: translate(50%, -50%);
         width: 1079.73px;
         height: auto;
         z-index: -1 !important;
         @media (max-width: 1280px){
            width: 700px;
         }
         @media (max-width: 992px){
            width: 500px;
         }
         @media (max-width: 768px){
            display: none;
         }
      }
      .decor {
         position: absolute;
         height: 100%;
         left: 50%;
         transform: translate(-50%, 0);
         z-index: 0;
         .line {
            position: absolute;
            width: 1px;
            height: 100%;
            z-index: 0;
            background-color: #fff;
            left: 34px;
            top: -57px;
            z-index: -1;
         }
         img{
            position: absolute;
            top: -70px;
            left: 0;
         }
      }
   }
}  
.about {
   z-index: 99;
   @media (max-width: 480px){
      text-align: center;
   }
   &__text {
      font-family: 'Raleway', sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 16px;
      line-height: 19px;
      color: #FFFFFF;
      margin: 28px 0;
      max-width: 565px;
   }
   &__btn {
      color: #464E2E;
      border: 1px solid #464E2E;
      padding: 12px 60px;
      z-index: 99;
      &:hover{ 
         background-color: #464E2E;
         color: #fff ;
      }
   }
}
