.section {
   &__katalog {
      z-index: 1;
      overflow: hidden;
   }
}
.katalog {
   display: flex;
   justify-content: center;
   align-items: center;
   gap: 24px;
   flex-wrap: wrap;
   &__item {
      background: #FFFFFF;
      box-shadow: 2px 2px 6px rgba(0, 0, 0, 0.25);
      max-width: 282px;
      width: 100%;
      z-index: 1;
   }
   &__item-title {
      font-family: 'Raleway', sans-serif;
      font-style: normal;
      font-weight: 300;
      font-size: 18px;
      line-height: 21px;
      letter-spacing: 0.1em;
      color: #000000;
      margin-bottom: 14px;
   }
   &__item-content{
      padding: 14px 24px;
      @media (max-width: 480px){
         display: flex;
         flex-direction: column;
         justify-content: center;
         align-items: center;
      }
   }
   &__item-btn {
      padding: 8px 32px;
      float: right;
      @media (max-width: 480px){
         float: none;
      }
   }
}